.message {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    color: #FFF;
    font-family: Montserrat, sans-serif;
    margin-bottom: 50px;

}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
    align-items: flex-end;

}

.imageAndText img {
    width: 48px;
    height: 48px
}

.text {
    /*background-image: url("../mes_bg.svg");*/
    /*background-size: cover;*/
    /*fill: #06C;*/
    /*filter: drop-shadow(0px 1px 2px rgba(29, 33, 38, 0.10)) drop-shadow(0px 5px 20px rgba(29, 33, 38, 0.03));*/
    background-color: #0059b3;
    border-radius: 5px;
    padding: 5px 10px;
    margin-right: 5px;
}

.text:after {


}

.name {
    display: flex;
    justify-content: flex-end;
    font-size: 18px;
    font-weight: 600;

}

.messageText {
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
}

.time {
    font-size: 10px;
    font-weight: 600;
    text-align: center;
    color:#000;
    margin: 0 10px 0 0;
}